uos que no poseen peligrosidad o riesgo de infección o contaminación al medio ambiente o a la persona. Entre ellos: 1. Biogradables, 2. Reciclables, 3. Inertes, 4. Ordinarios.")])]),_c('div',{staticClass:"tarjeta BG03 p-3",attrs:{"x":"91.5%","y":"49%"}},[_c('p',[_vm._v("Son aquellos residuos que por sus características no puede ser aprovechados y por lo tanto debe darse una disposición final adecuada. ")])]),_c('div',{staticClass:"tarjeta BG03 p-3",attrs:{"x":"8.6%","y":"74.2%"}},[_c('p',[_vm._v("Son todos aquellos residuos provenientes de partes y/o restos humanos, como muestras de tejidos, biopsias, amputaciones, restos de cadáveres y fluidos corporales.")])]),_c('div',{staticClass:"tarjeta BG03 p-3",attrs:{"x":"25.1%","y":"74.2%"}},[_c('p',[_vm._v("Son todos los elementos resultantes de los procesos realizados donde el material esté contaminado con sangre o fluidos.")])]),_c('div',{staticClass:"tarjeta BG03 p-3",attrs:{"x":"41.7%","y":"74.2%"}},[_c('p',[_vm._v("Son los instrumentos que por su capacidad cortante o punzante puedan ocasionar un accidente percutáneo. Acá se encuentran las agujas, las hojas de bisturí, trocar y cuchillas.")])]),_c('div',{staticClass:"tarjeta BG03 p-3",attrs:{"x":"58.3%","y":"74.2%"}},[_c('p',[_vm._v("Son los restos de animales usados para fines de investigación, muestra o testeo, los cuales se encuentran contaminados con un agente infeccioso.")])])]),_vm._m(4),_vm._m(5)],1),_vm._m(6),_vm._m(7),_vm._m(8),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-10 mb-4"},[_c('LineaTiempoD',{staticClass:"color-primario mb-4"},[_c('div',{staticClass:"row justify-content-center",attrs:{"numero":"1","titulo":"Diagnóstico ambiental y sanitario"}},[_c('div',{staticClass:"col-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema4/img08.png")}})])]),_c('div',{staticClass:"col-lg-6"},[_c('p',[_vm._v("Se debe generar la caracterización de los residuos generados, tanto de forma cualitativa como cuantita